.header{padding:1rem}@media(min-width: 1024px){.header{padding:2rem;padding-bottom:0}}.header__nav-menu{display:flex}@media(max-width: 1024px){.header__nav-menu{justify-content:space-between}}.header__nav-link{color:var(--foreground);padding:.75rem 1.5rem;display:inline-block}.header__nav-link--active{border:2px solid var(--support);border-radius:50%}